import Big from 'big.js';
import { UnitEnum } from './unit.enum';

/**
 * factor for 1 degree in radians.
 */
export const degreeScaleFactor = Number.parseFloat(new Big(Math.PI).div(180).toFixed());

/**
 * factor for 1 kilogram meter in newton meters.
 */
export const gravitationalAcceleration = 9.80665;

export interface UnitType {
    quantity: UnitEnum;
}

/**
 * Length unit type.
 */
export class Length implements UnitType {
    public readonly quantity = UnitEnum.LENGTH;
    private readonly length = true; // needed for generics type checking
}

/**
 * Angle unit type.
 */
export class Angle implements UnitType {
    public readonly quantity = UnitEnum.PLANE_ANGLE;
    private readonly angle = true; // needed for generics type checking
}

/**
 * Mass unit type.
 */
export class Mass implements UnitType {
    public readonly quantity = UnitEnum.MASS;
    private readonly mass = true; // needed for generics type checking
}

/**
 * Voltage unit type.
 */
export class Voltage implements UnitType {
    public readonly quantity = UnitEnum.VOLTAGE;
    private readonly voltage = true;
}

/**
 * Current unit type.
 */
export class Current implements UnitType {
    public readonly quantity = UnitEnum.CURRENT;
    private readonly current = true;
}

/**
 * Time unit type.
 */
export class Time implements UnitType {
    public readonly quantity = UnitEnum.TIME;
    private readonly time = true;
}

/**
 * Force unit type.
 */
export class Force implements UnitType {
    public readonly quantity = UnitEnum.FORCE;
    private readonly force = true;
}

/**
 * Speed unit type.
 */
export class Speed implements UnitType {
    public readonly quantity = UnitEnum.SPEED;
    private readonly speed = true;
}

/**
 * Angular speed unit type.
 */
export class AngularSpeed implements UnitType {
    public readonly quantity = UnitEnum.ANGULAR_SPEED;
    private readonly angularSpeed = true;
}

/**
 * Acceleration unit type.
 */
export class Acceleration implements UnitType {
    public readonly quantity = UnitEnum.ACCELERATION;
    private readonly acceleration = true;
}

/**
 * Angular acceleration unit type.
 */
export class AngularAcceleration implements UnitType {
    public readonly quantity = UnitEnum.ANGULAR_ACCELERATION;
    private readonly angularAcceleration = true;
}

/**
 * Power unit type.
 */
export class Power implements UnitType {
    public readonly quantity = UnitEnum.POWER;
    private readonly power = true;
}

/**
 * Torque unit type.
 */
export class Torque implements UnitType {
    public readonly quantity = UnitEnum.TORQUE;
    private readonly torque = true;
}

/**
 * Momentum unit type.
 */
export class Momentum implements UnitType {
    public readonly quantity = UnitEnum.MOMENTUM;
    private readonly momentum = true;
}

/**
 * Moment of Inertia unit type.
 */
export class MomentOfInertia implements UnitType {
    public readonly quantity = UnitEnum.MOMENT_INERTIA;
    private readonly momentOfInertia = true;
}

/**
 * Temperature unit type
 */
export class Temperature implements UnitType {
    public readonly quantity = UnitEnum.TEMPERATURE;
    private readonly temperature = true;
}

/**
 * Unit definition.
 */
export interface Unit<T extends UnitType = UnitType> {
    /**
     * Unit type.
     */
    quantity: T;
    /**
     * Factor for converting to SI unit.
     */
    scaleFactor: number;
    /**
     * Number of significant digits.
     */
    significantDigits: number;
    /**
     * Unit label.
     */
    label: string;
    /**
     * Unit symbol.
     */
    symbol: string;
}

export class DerivedUnit<T extends UnitType = UnitType> implements Unit {
    /**
     * Factor for converting to SI unit.
     */
    scaleFactor: number;
    /**
     * Unit label.
     */
    label: string;
    /**
     * Unit symbol.
     */
    symbol: string;

    /** Creates a new derived unit with the specified new unit type, using the specified input units to derive it
     *
     * @param quantity The quantity type of the new unit
     * @param numeratorUnit The numerator of the new unit
     * @param denominatorUnit The denominator of the new unit
     * @param significantDigits The number of significant digits for the new unit
     */
    constructor(
        public quantity: T,
        public numeratorUnit: Unit,
        public denominatorUnit: Unit,
        public significantDigits: number,
    ) {
        this.label = this.numeratorUnit.label.endsWith(denominatorUnit.label)
            ? `${this.numeratorUnit.label}^2`
            : `${this.numeratorUnit.label}/${this.denominatorUnit.label}`;
        this.symbol = this.numeratorUnit.symbol.endsWith(denominatorUnit.symbol)
            ? `${this.numeratorUnit.symbol}²`
            : `${this.numeratorUnit.symbol}/${this.denominatorUnit.symbol}`;
        this.scaleFactor = this.numeratorUnit.scaleFactor * this.denominatorUnit.scaleFactor;
    }
}

// Base units

/**
 * Unit of length in mm.
 */
export const mm = {
    quantity: new Length(),
    scaleFactor: 0.001,
    significantDigits: 1,
    label: 'mm',
    symbol: 'mm',
} as Unit<Length>;

/**
 * Unit of length in m.
 */
export const m = {
    quantity: new Length(),
    scaleFactor: 1,
    significantDigits: 4,
    label: 'm',
    symbol: 'm',
} as Unit<Length>;

/**
 * Array of all supported length units.
 */
export const lengthUnits: Array<Unit<Length>> = [mm, m];

/**
 * Unit of length in radians.
 */
export const rad = {
    quantity: new Angle(),
    scaleFactor: 1,
    significantDigits: 4,
    label: 'rad',
    symbol: 'rad',
} as Unit<Angle>;

/**
 * Unit of length in degrees.
 */
export const deg = {
    quantity: new Angle(),
    scaleFactor: degreeScaleFactor,
    significantDigits: 2,
    label: 'deg',
    symbol: '°',
} as Unit<Angle>;

/**
 * Array of all supported angle units.
 */
export const angleUnits: Array<Unit<Angle>> = [rad, deg];

/**
 * Unit of mass in grams.
 */
export const g = {
    quantity: new Mass(),
    scaleFactor: 0.001,
    significantDigits: 0,
    label: 'g',
    symbol: 'g',
} as Unit<Mass>;

/**
 * Unit of mass in kilograms.
 */
export const kg = {
    quantity: new Mass(),
    scaleFactor: 1,
    significantDigits: 3,
    label: 'kg',
    symbol: 'kg',
} as Unit<Mass>;

/**
 * Array of all supported mass units.
 */
export const massUnits: Array<Unit<Mass>> = [g, kg];

/**
 * Unit of voltage in volts.
 */
export const V = {
    quantity: new Voltage(),
    scaleFactor: 1,
    significantDigits: 3,
    label: 'V',
    symbol: 'V',
} as Unit<Voltage>;

/**
 * Unit of voltage in millivolts.
 */
export const mV = {
    quantity: new Voltage(),
    scaleFactor: 0.001,
    significantDigits: 0,
    label: 'mV',
    symbol: 'mV',
} as Unit<Voltage>;

/**
 * Array of all supported voltage units.
 */
export const voltageUnits: Array<Unit<Voltage>> = [V, mV];

/**
 * Unit of current in ampere.
 */
export const A = {
    quantity: new Current(),
    scaleFactor: 1,
    significantDigits: 5,
    label: 'A',
    symbol: 'A',
} as Unit<Current>;

/**
 * Unit of current in milliampere.
 */
export const mA = {
    quantity: new Current(),
    scaleFactor: 0.001,
    significantDigits: 2,
    label: 'mA',
    symbol: 'mA',
} as Unit<Current>;

/**
 * Array of all supported current units.
 */
export const currentUnits: Array<Unit<Current>> = [A, mA];

/**
 * Unit of time in seconds.
 */
export const s = {
    quantity: new Time(),
    scaleFactor: 1,
    significantDigits: 2,
    label: 's',
    symbol: 's',
} as Unit<Time>;

/**
 * Unit of time in milliseconds.
 */
export const ms = {
    quantity: new Time(),
    scaleFactor: 0.001,
    significantDigits: 0,
    label: 'ms',
    symbol: 'ms',
} as Unit<Time>;

/**
 * Array of all supported time units.
 */
export const timeUnits: Array<Unit<Time>> = [s, ms];

/**
 * Unit of force in newton.
 */
export const N = {
    quantity: new Force(),
    scaleFactor: 1,
    significantDigits: 1,
    label: 'N',
    symbol: 'N',
} as Unit<Force>;

/**
 * Array of all supported force units.
 */
export const forceUnits: Array<Unit<Force>> = [N];

/**
 * Unit of power in watts.
 */
export const W = {
    quantity: new Power(),
    scaleFactor: 1,
    significantDigits: 1,
    label: 'W',
    symbol: 'W',
} as Unit<Power>;

/**
 * Array of all supported power units.
 */
export const powerUnits: Array<Unit<Power>> = [W];

/**
 * Unit of torque in kilogram meters.
 */
export const kgM = {
    quantity: new Torque(),
    scaleFactor: gravitationalAcceleration,
    significantDigits: 1,
    label: 'kg*m',
    symbol: 'kg·m',
} as Unit<Torque>;

/**
 * Unit of moment of inertia in kilogram meters squared
 */
export const kgMSq = {
    quantity: new MomentOfInertia(),
    scaleFactor: 1,
    significantDigits: 6,
    label: 'kg*m*m',
    symbol: 'kg·m²',
} as Unit<MomentOfInertia>;

/**
 * Unit of temperature in degrees Celsius
 */
export const degreesCelsius = {
    quantity: new Temperature(),
    scaleFactor: 1,
    significantDigits: 2,
    label: 'C',
    symbol: '°',
} as Unit<Temperature>;

/**
 * Array of all supported torque units.
 */
export const torqueUnits: Array<Unit<Torque>> = [kgM];

// Derived Units

/**
 * Unit of speed in meters per second.
 */
export const mPerSec = new DerivedUnit(new Speed(), m, s, 5);

/**
 * Unit of speed in millimeters per second.
 */
export const mmPerSec = new DerivedUnit(new Speed(), mm, s, 2);

/**
 * Array of all supported speed units.
 */
export const speedUnits: Array<DerivedUnit<Speed>> = [mPerSec, mmPerSec];

/**
 * Unit of angular speed in radiant per second.
 */
export const radPerSec = new DerivedUnit(new AngularSpeed(), rad, s, 3);

/**
 * Unit of angular speed in degree per second.
 */
export const degPerSec = new DerivedUnit(new AngularSpeed(), deg, s, 2);

/**
 * Array of all supported angular speed units.
 */
export const angularSpeedUnits: Array<DerivedUnit<AngularSpeed>> = [radPerSec, degPerSec];

/**
 * Unit of acceleration in meter per second squared.
 */
export const mPerSecSq = new DerivedUnit(new Acceleration(), mPerSec, s, 5);

/**
 * Unit of acceleration in millimeter per second squared.
 */
export const mmPerSecSq = new DerivedUnit(new Acceleration(), mmPerSec, s, 2);

/**
 * Array of all supported acceleration units.
 */
export const accelerationUnits: Array<DerivedUnit<Acceleration>> = [mPerSecSq, mmPerSecSq];

/**
 * Unit of angular acceleration in radian per second squared.
 */
export const radPerSecSq = new DerivedUnit(new AngularAcceleration(), radPerSec, s, 3);

/**
 * Unit of angular acceleration in degree per second squared.
 */
export const degPerSecSq = new DerivedUnit(new AngularAcceleration(), degPerSec, s, 2);

/**
 * Array of all supported angular acceleration units.
 */
export const angularAccelerationUnits: Array<DerivedUnit<AngularAcceleration>> = [radPerSecSq, degPerSecSq];

/**
 * Unit of momentum in kilogram meters per second.
 */
export const kgMPerSec = new DerivedUnit(new Momentum(), kgM, s, 1);

/**
 * Array of all supported momentum units.
 */
export const momentumUnits: Array<DerivedUnit<Momentum>> = [kgMPerSec];

export const momentInertiaUnits: Array<Unit<MomentOfInertia>> = [kgMSq];

/**
 * Array of all supported temperature units
 */
export const temperatureUnits: Array<Unit<Temperature>> = [degreesCelsius];

/**
 * All unit types.
 */
export type UnitTypes =
    | Mass
    | Length
    | Angle
    | Voltage
    | Current
    | Speed
    | AngularSpeed
    | Acceleration
    | AngularAcceleration
    | Time
    | Torque
    | Force
    | Power
    | Momentum
    | Temperature;

/**
 * All units.
 */
export const allUnits: Array<Unit> = [
    ...lengthUnits,
    ...angleUnits,
    ...massUnits,
    ...voltageUnits,
    ...currentUnits,
    ...speedUnits,
    ...angularSpeedUnits,
    ...accelerationUnits,
    ...angularAccelerationUnits,
    ...timeUnits,
    ...torqueUnits,
    ...forceUnits,
    ...powerUnits,
    ...momentumUnits,
    ...momentInertiaUnits,
    ...temperatureUnits,
];

/**
 * Map from unit enumeration to SI unit.
 */
export const CONCEPT_UNITS: { [key: string]: Unit<any> } = {
    [UnitEnum.LENGTH]: m,
    [UnitEnum.MASS]: kg,
    [UnitEnum.PLANE_ANGLE]: rad,
    [UnitEnum.VOLTAGE]: V,
    [UnitEnum.CURRENT]: A,
    [UnitEnum.ACCELERATION]: mPerSecSq,
    [UnitEnum.ANGULAR_ACCELERATION]: radPerSecSq,
    [UnitEnum.SPEED]: mPerSec,
    [UnitEnum.ANGULAR_SPEED]: radPerSec,
    [UnitEnum.TIME]: s,
    [UnitEnum.TORQUE]: kgM,
    [UnitEnum.FORCE]: N,
    [UnitEnum.POWER]: W,
    [UnitEnum.MOMENTUM]: kgMPerSec,
    [UnitEnum.MOMENT_INERTIA]: kgMSq,
    [UnitEnum.TEMPERATURE]: degreesCelsius,
};
