import { FixedPointNumber } from './fixed-point-number';
import { Quantity } from './quantity';
import { Angle, AngularSpeed, Current, Force, Length, Mass, Momentum, Speed, Time, Torque, Unit, UnitType, Voltage } from './unit';

type GenericConverter<T extends UnitType> = (value: FixedPointNumber<T>, to: Unit<T>) => FixedPointNumber<T>;

/**
 *
 * @param value. The value to convert from
 * @param to. The unit to convert to.
 */
export function converter<T extends UnitType>(value: FixedPointNumber<T>, to: Unit<T>): FixedPointNumber<T> {
    const fromUnit: Unit<T> = value.getUnit();
    const quantity: Quantity<T> = new Quantity(value.getBigValue(), fromUnit);
    return new FixedPointNumber(quantity.getAs(to), to);
}

/**
 * Converter for mass unit types.
 */
export const massConverter: GenericConverter<Mass> = converter;

/**
 * Converter for length unit types.
 */
export const lengthConverter: GenericConverter<Length> = converter;

/**
 * Converter for angle unit types.
 */
export const angleConverter: GenericConverter<Angle> = converter;

/**
 * Converter for angular speed unit types.
 */
export const angularSpeedConverter: GenericConverter<AngularSpeed> = converter;

/**
 * Converter for voltage unit types.
 */
export const voltageConverter: GenericConverter<Voltage> = converter;

/**
 * Converter for current unit types.
 */
export const currentConverter: GenericConverter<Current> = converter;

/**
 * Converter for force unit types.
 */
export const forceConverter: GenericConverter<Force> = converter;

/**
 * Converter for power unit types.
 */
export const powerConverter: GenericConverter<Force> = converter;

/**
 * Converter for torque unit types.
 */
export const torqueConverter: GenericConverter<Torque> = converter;

/**
 * Converter for momentum unit types.
 */
export const momentumConverter: GenericConverter<Momentum> = converter;

/**
 * Converter for speed unit types.
 */
export const speedConverter: GenericConverter<Speed> = converter;

/**
 * Converter for time unit types.
 */
export const timeConverter: GenericConverter<Time> = converter;

export {};
